*, :before, :after {
  box-sizing: border-box;
}

body {
  background-color: #e9f2fd;
}

:root {
  --select-border: #777;
  --select-focus: blue;
  --select-arrow: var(--select-border);
}

select {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  margin: 0;
  padding: 0 1em 0 0;
}

.select {
  width: 640px;
  min-width: 15ch;
  border: 1px solid var(--select-border);
  cursor: pointer;
  background-color: #fff;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
  border-radius: .25em;
  grid-template-areas: "select";
  align-items: center;
  padding: .25em .5em;
  font-size: 1.25rem;
  line-height: 1.1;
  display: grid;
  position: relative;
}

select:focus + .focus {
  border: 2px solid var(--select-focus);
  border-radius: inherit;
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: -1px;
  right: -1px;
}

div.cat-info {
  gap: 10px;
  display: flex;
}

div.cat-info h2 {
  margin-top: 0;
}

div.cat-info img, div.cat-info div {
  width: 320px;
}

.hiden-selector {
  display: none !important;
}

.loader {
  color: var(--select-focus);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 640px;
  background: linear-gradient(#ff3d00 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 2s linear infinite;
  position: absolute;
  bottom: -5px;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

